<template>
  <div id="style-operate">
    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="900" draggable>
      <el-form :model="form" label-width="120px" :rules="rules" ref="ruleForm">
        <el-row>
          <el-col :span="6"><div class="grid-content ep-bg-purple" />
            <el-upload class="avatar-uploader" action="/api/uploadFile/uploadFile" :data="fileData" :headers="headers" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
              <img v-if="form.urlStr" :src="form.urlStr" class="avatar"/>
              <el-icon v-else class="avatar-uploader-icon"><Plus/></el-icon>
            </el-upload>
          </el-col>
          <el-col :span="18"><div class="grid-content ep-bg-purple-light" />
            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('UserView.account')" :label-width="formLabelWidth" prop="account">
                  <el-input :readonly="isReadonly" v-model="form.account" :placeholder="$t('UserView.accountPlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('UserView.password')" prop="password" :label-width="formLabelWidth" v-if="this.type == 'add'">
                  <el-input :readonly="isReadonly" v-model="form.password" :placeholder="$t('UserView.passwordPlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('UserView.nickName')" prop="nickName" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.nickName" :placeholder="$t('UserView.nickNamePlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('UserView.name')" prop="name" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.name" :placeholder="$t('UserView.namePlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('UserView.sex')" prop="sex" :label-width="formLabelWidth">
                  <el-select filterable v-model="form.sex" :placeholder="$t('UserView.sexPlaceholder')" :disabled="isReadonly">
                    <el-option :value="0" :label="$t('pullDown.sex.secret')"/>
                    <el-option :value="1" :label="$t('pullDown.sex.man')"/>
                    <el-option :value="2" :label="$t('pullDown.sex.woman')"/>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('UserView.role')" prop="role" :label-width="formLabelWidth">
                  <el-select filterable v-model="form.role" :placeholder="$t('UserView.rolePlaceholder')">
                    <el-option v-for="item in selectData.roleList" :key="item.id" :label="item.name"
                               :value="item.id"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('UserView.region')" prop="region" :label-width="formLabelWidth">
                  <el-select v-model="form.region" filterable :placeholder="$t('UserView.regionPlaceholder')" :disabled="isReadonly" >
                    <el-option v-for="item in selectData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('UserView.phone')" prop="phone" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.phone" :placeholder="$t('UserView.phonePlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('UserView.identityCard')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.identityCard" :placeholder="$t('UserView.identityCardPlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('UserView.birthDate')" :label-width="formLabelWidth">
                  <el-date-picker v-model="form.birthDate" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD"
                                  :placeholder="$t('UserView.birthDatePlaceholder')" style="width: 300px;"></el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('UserView.weixin')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.weixin" :placeholder="$t('UserView.weixinPlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('UserView.qq')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.qq"  :placeholder="$t('UserView.qqPlaceholder')" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple-dark" />
                <el-form-item :label="$t('UserView.userStatus')" :label-width="formLabelWidth">
                  <el-select v-model="form.userStatus" :placeholder="$t('UserView.userStatusPlaceholder')">
                    <el-option :value="1" :label="$t('pullDown.userStatus.enable')"/>
                    <el-option :value="2" :label="$t('pullDown.userStatus.disable')"/>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-dark" />
                <el-form-item :label="$t('UserView.email')" prop="email" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.email" :placeholder="$t('UserView.emailPlaceholder')" autocomplete="off" style="width: 100%;"/>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row v-if="type != 'add'">
              <el-col :span="24"><div class="grid-content ep-bg-purple-dark" />
                <el-form-item :label="$t('UserView.regions')" prop="regions" :label-width="formLabelWidth">
                  <el-select
                      v-model="form.regions"
                      multiple
                      filterable
                      collapse-tags
                      style="width: 100%;"
                      :placeholder="$t('UserView.regionsPlaceholder')">
                    <el-option v-for="item in selectData.regionList" :key="item.id" :label="item.name" :value="item.id">
                      <span style="float: left">{{ item.name }}</span>
                      <span style="float: right; color: var(--el-text-color-secondary); font-size: 13px;">{{ item.code }}</span>
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-form>

      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" v-show="type != 'detail'" @click="submitButton()">
          {{ $t('common.button.confirm') }}
        </el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {getRolePullDown} from "@/http/PullDownApi";
import {ElNotification} from "element-plus";
import {getUserAdminById, saveUserAdmin, updateUserAdmin} from "@/http/basics/userApi";
import {getRegionPullDown} from "@/http/clienteleApi";

export default {
  name: "style-operate",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据
      rowData: {},
      // 行数据副本
      form: {},
      // 表单验证规则
      rules: {
        account: [
          { required: true, message: this.$t('UserView.accountPlaceholder'), trigger: 'blur' },
          { min: 6, max: 20, message: this.$t('UserView.passwordLength'), trigger: 'blur' },
        ],
        password: [
          { required: true, message: this.$t('UserView.passwordPlaceholder'), trigger: 'blur' },
          { min: 6, max: 20, message: this.$t('UserView.passwordLength'), trigger: 'blur' },
        ],
        nickName: [
          { required: true, message: this.$t('UserView.nickNamePlaceholder'), trigger: 'blur' },
        ],
        name: [
          { required: true, message: this.$t('UserView.namePlaceholder'), trigger: 'blur' },
        ],
        sex: [
          { required: true, message: this.$t('UserView.sexPlaceholder'), trigger: 'change' },
        ],
        role: [
          { required: true, message: this.$t('UserView.rolePlaceholder'), trigger: 'change' },
        ],
        region: [
          { required: true, message: this.$t('UserView.regionPlaceholder'), trigger: 'change' },
        ],
        phone: [
          { required: true, message: this.$t('UserView.phonePlaceholder'), trigger: 'blur' },
          { min: 11, max: 11, message: this.$t('UserView.phoneLength'), trigger: 'blur' },
        ],
        email: [
          { required: true, message: this.$t('UserView.emailPlaceholder'), trigger: 'blur' },
          { type: 'email', message: this.$t('UserView.emailError'), trigger: ['blur', 'change'] }
        ],
        regions: [
          { required: true, message: this.$t('UserView.regionsPlaceholder'), trigger: 'change' },
        ],
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '90px',

      selectData: {}, // 下拉的数据集合对象

      // 上传请求的请求头 存放token校验
      headers: {},
      // 请求接口需要携带的参数
      fileData: { folder: 'userPhotoUrl' },
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      // newVal - 新值, oldVal - 旧值
      if (newVal == '') return
      if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
      if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
      if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  created() {
    // 后端如果验证 token 会报空 因为请求不是异步不会被路由守卫装配token 所以需要在发送请求时带上token
    this.headers = {token: localStorage.getItem("token")};
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getPullDownData();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getPullDownData() {
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.regionList = res.data.data;
      }).catch((erro) => { });
      // 获取角色的数据
      getRolePullDown().then(res => {
        if (res.data.status == 200) this.selectData.roleList = res.data.data;
      }).catch((erro) => { });
    },

    getUserById(id) {
      getUserAdminById({ id: id}).then(res => {
        if (res.data.status == 200) {
          this.form = res.data.data;
          this.form.regions = res.data.data.regions;
          this.form.urlStr = process.env.VUE_APP_BACKEND_API_URL + res.data.data.photoUrl;
        }
      }).catch(error => { });
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.$refs.ruleForm.resetFields(); // 重置表单验证
      this.dialogFormVisible = false;
      this.isReadonly = false;
      this.form = {};
      this.rowData = {};
    },
    // 确认按钮
    submitButton() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.type == "add") {
            saveUserAdmin(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          } else if (this.type == "edit") {
            updateUserAdmin(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    },


    // 上传之后的res结果集
    handleAvatarSuccess(res, file) {
      if (res.status == 200) {
        // vue.config.js 定义了后端请求前缀路径 可直接调用
        this.form.photoUrl = res.data;
        this.form.urlStr = process.env.VUE_APP_BACKEND_API_URL + res.data;
      } else {
        this.$message.error(this.$t('common.message.networkError'));
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadError'), type: 'info',})
      }
      if (!isLt2M) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadSizeError'), type: 'info',})
      }
      return isJPG && isLt2M;
    }
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}

.el-select {
  width: 300px;
}

.el-input {
  width: 300px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>
